import React from 'react';
import './policy.css';

const Policy = () => {
    return (
        <div className='policy_container'>
            <h1>Privacy Policy</h1>
            <p>
                At <strong>MASH Automation</strong>, we are committed to protecting the privacy and security of our users' personal information. 
                This Privacy Policy explains how we collect, use, and safeguard the information you provide to us when using our chatbot services.
            </p>
            
            <h2>Information We Collect</h2>
            <p>We collect the minimum amount of information necessary to provide our chatbot services effectively. This may include:</p>
            <ul>
                <li>Your WhatsApp phone number or associated identifier</li>
                <li>Messages you send to our chatbot</li>
                <li>Analytics data related to your chatbot interactions (e.g., session duration, user flows)</li>
            </ul>
            <p>We do not collect any other personal information such as your name, email address, or location data unless you voluntarily provide it.</p>
            
            <h2>Use of Information</h2>
            <p>We use the information we collect solely for the purpose of operating and improving our chatbot services. Specifically, we may use your information to:</p>
            <ul>
                <li>Respond to your messages and queries</li>
                <li>Analyze usage patterns to enhance chatbot functionality</li>
                <li>Maintain and secure our systems and infrastructure</li>
            </ul>
            <p>We do not sell, rent, or share your personal information with any third parties for marketing or advertising purposes.</p>
            
            <h2>Data Security</h2>
            <p>We implement robust security measures to protect your information from unauthorized access, disclosure, alteration, or destruction. 
            This includes industry-standard encryption protocols, access controls, and regular security audits.</p>
            
            <h2>Data Retention</h2>
            <p>We retain your information only for as long as necessary to fulfill the purposes outlined in this Privacy Policy, unless a longer retention period is required or permitted by law.</p>
            
            <h2>Your Rights</h2>
            <p>You have the right to access, rectify, or delete your personal information held by us. You may also object to or restrict the processing of your information in certain circumstances. To exercise these rights, please contact us using <a href="mailto:work@mashautomation.in">work@mashautomation.in</a>.</p>
            
            <h2>Changes to this Privacy Policy</h2>
            <p>We may update this Privacy Policy from time to time to reflect changes in our practices or legal requirements. The latest version will always be available on our website, and we encourage you to review it periodically.</p>
            
            <h2>Contact Us</h2>
            <p>If you have any questions or concerns about this Privacy Policy or our data practices, please contact our Data Protection Officer at <a href="mailto:work@mashautomation.in">work@mashautomation.in</a>.</p>
            
            <p>
                By using our chatbot services, you consent to the collection, use, and processing of your information as described in this Privacy Policy.<br />
                <strong>Effective Date: 11 April 2024</strong>
            </p>
        </div>
    );
};

export default Policy;
