import React from "react";
import "./header.css";
import logo from "../assets/images/logo-mash.jpeg";
import { Link } from "react-router-dom";

const Header = () => {

    const handleClick = () => {
        window.open("https://calendly.com/mashautomation/30min", "_blank");
    }

    return (
        <header style={{position: "sticky", top: "0", zIndex: "1000"}}>
            <div className="header">
                <Link to="/"><img className="logo" src={logo} alt="logo" /></Link>
                <div className="navigations">
                    <a href="#about">About</a>
                    <a href="#testimonials">Testimonials</a>
                    <a href="#how_we_work">How We Work</a>

                </div>
                <button className="btn btn-primary fs-6" onClick={handleClick} >Get Started</button>
            </div>
        </header>
    );
};

export default Header;
