import React, { useEffect, useRef, useState } from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import './carousel.css'; // Create and import your CSS file

import make from "../../assets/icons/make.png";
import manychat from "../../assets/icons/manychat.png";
import openAI from "../../assets/icons/openAI.png";
import shopify from "../../assets/icons/shopify.png";
import slack from "../../assets/icons/slack.png";
import stripe from "../../assets/icons/stripe.png";
import zapier from "../../assets/icons/zapier.png";
import voiceFlow from "../../assets/icons/voiceFlow.png";
import botpress from "../../assets/icons/botpress.png";
import synthesia from "../../assets/icons/synthesia.png";
import airtable from "../../assets/icons/airtable.png";

const Carousel = () => {
  const sliderRef = useRef(null);

  const settings = {
    infinite: true,
    variableWidth: true,
    arrows: false,
    accessibility: false,
    draggable: false,
    swipe: false,
    touchMove: false,
    speed: 3000, // Speed of the transition
    autoplay: true, // Enable autoplay
    autoplaySpeed: 0, // Set to 0 for continuous motion
    cssEase: 'linear', // Ensure the transition is smooth
  };

  return (
    <div className="slider__container">
      <Slider ref={sliderRef} {...settings}>
        <div className="slider__item"><img src={make} alt="Slide 1" /></div>
        <div className="slider__item"><img src={manychat} style={{ width: "80px", height: "80px" }} alt="Slide 2" /></div>
        <div className="slider__item"><img src={openAI} style={ { width: "100px", height: "60px" }} alt="Slide 3" /></div>
        <div className="slider__item"><img src={shopify} alt="Slide 4" /></div>
        <div className="slider__item"><img src={slack} alt="Slide 5" /></div>
        <div className="slider__item"><img src={stripe} style={{ width: "100px", height: "50px" }} alt="Slide 6" /></div>
        <div className="slider__item"><img src={zapier} style={{ width: "100px", height: "100px" }} alt="Slide 7" /></div>
        <div className="slider__item"><img src={botpress} style={{ width: "100px", height: "100px" }} alt="Slide 8" /></div>
        <div className="slider__item"><img src={airtable} style={{ width: "60px", height: "60px" }} alt="Slide 9" /></div>
        <div className="slider__item"><img src={synthesia} alt="Slide 10" /></div>
        <div className="slider__item"><img src={voiceFlow} alt="Slide 11" /></div>
      </Slider>
    </div>
  );
};

export default Carousel;
