import React from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter } from "react-router-dom"; // Import BrowserRouter
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js";
import Header from "./header/header";
import VoiceflowChat from "./home_components/chat/chat";

function DeviceSpecificApp() {
  const [isDesktop, setIsDesktop] = React.useState(window.innerWidth > 768); // 768px is a common breakpoint for mobile devices

  React.useEffect(() => {
    function handleResize() {
      setIsDesktop(window.innerWidth > 1200);
    }

    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  if (!isDesktop) {
    return (
      <div className="little-screen">
        <h2>Sorry, this website is not optimized for mobile devices YET.</h2>
        <h1 id='laptop'>Please visit us on a desktop or laptop computer .</h1>
      </div>
    );
  }

  return (
    <React.StrictMode>
      <App />
    </React.StrictMode>
  );
}

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <BrowserRouter> {/* Wrap the DeviceSpecificApp component with BrowserRouter */}
      <DeviceSpecificApp />
    </BrowserRouter>
  </React.StrictMode>
);

reportWebVitals();
