import React, { useEffect, useRef } from 'react';
import './service_title.css';
import gsap from 'gsap';
import { useInView } from 'react-intersection-observer';

const GevStack = ({ title, animationSpeed = 6 }) => {
    const headingRef = useRef(null);
    const [ref, inView] = useInView({
        triggerOnce: true,
        rootMargin: '-50px 0px', // Adjust the root margin as needed
    });

    useEffect(() => {
        if (inView) {
            const heading = headingRef.current;
            const text = heading.textContent;
            heading.innerHTML = text.split(' ').map(word => {
                return word.split('').map(char => `<span>${char}</span>`).join('') + `<span>&nbsp;</span>`;
            }).join('');

            const headingChars = heading.querySelectorAll('span');

            gsap.from(headingChars, {
                filter: "blur(0.15em)",
                stagger: {
                    from: "left",
                    each: 0.1 / animationSpeed  // Adjust stagger speed if needed
                },
                duration: i => (1.25 + i * 0.75) / animationSpeed,
                ease: "power2.inOut"
            });

            gsap.from(headingChars, {
                xPercent: i => (i + 1) * 20,
                opacity: 0,
                stagger: {
                    from: "left",
                    each: 0.1 / animationSpeed  // Adjust stagger speed if needed
                },
                duration: i => (1 + i * 0.85) / animationSpeed,
                ease: "power2.out"
            }, "<");
        }
    }, [title, animationSpeed, inView]);

    return (
        <div className="container">
            <div ref={ref}>
                <h1 className="heading" ref={headingRef} aria-label="nebula">
                    {title}
                </h1>
            </div>
        </div>
    );
}

export default GevStack;
